import React , {Component} from 'react';

class Intro_1 extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomInRight">
                <section className=" bg-fouad text-primary text-center p-3">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h5 className="display-4">Vision Future De
                                    L'entreprise</h5>
                                <p className="lead">Face aux changements climatiques et
                                    en appui au renforcement des capacités énergétiques nationales, IDROLEC, a accéléré et
                                    accentué sa stratégie nationale de développement et de
                                    promotion des énergies renouvelables.
                                    Elle compte dorénavant intervenir
                                    grâce à son expérience,, ses ressources
                                    humaines qualifiées et ses partenaires
                                    nationaux, régionaux et internationaux
                                    dans la maitrise et l’efficacité énergétique.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Intro_1;