import React , {Component} from 'react';

class Carousel extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomInLeft">
                <div className="bd-example">
                    <div id="carouselExampleCaptions" className="carousel slide" data-ride="carousel">
                        <div className="carousel-inner">
                            <div className="carousel-item active animated jello">
                                <img src="../images/eau1.jpg" className="d-block w-100" alt="..."/>
                                    <div className="carousel-caption d-none d-md-block"></div>
                            </div>
                            <div className="carousel-item animated zoomIn">
                                <img src="../images/electricity1.jpg" className="d-block w-100" alt="..."/>
                                    <div className="carousel-caption d-none d-md-block"></div>
                            </div>
                            <div className="carousel-item animated rubberBand">
                                <img src="../images/solar%20panel%201.jpg" className="d-block w-100" alt="..."/>
                                    <div className="carousel-caption d-none d-md-block"></div>
                            </div>
                            <div className="carousel-item animated flash">
                                <img src="../images/wind%20tower.jpg" className="d-block w-100" alt="..."/>
                                <div className="carousel-caption d-none d-md-block"></div>
                            </div>
                        </div>
                        <a className="carousel-control-prev" href="#carouselExampleCaptions" role="button"
                           data-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="sr-only">Previous</span>
                        </a>
                        <a className="carousel-control-next" href="#carouselExampleCaptions" role="button"
                           data-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="sr-only">Next</span>
                        </a>
                    </div>
                </div>            </div>
        );
    }
}
export default Carousel;