import React , {Component} from 'react';

class Course_section extends Component{
    render() {
        return(
            <div className="animated zoomIn">
                <section className="p-3 text-center">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h2 className="font-weight-bold text-teal">Veuillez trouver les services que nous vous proposons</h2>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4">
                                <a href="../images/eau2.jpg" data-toggle="lightbox">
                                    <img src="../images/eau2.jpg" className="img-fluid c-image" alt=""/>
                                    <h4 className="text-dark">EAU</h4>
                                </a>
                            </div>
                            <div className="col-md-4">
                                <a href="../images/electricity2.jpg" data-toggle="lightbox">
                                    <img src="../images/electricity2.jpg" className="img-fluid c-image" alt=""/>
                                    <h4 className="text-dark">ÉLECTRICITÉ</h4>
                                </a>
                            </div>
                            <div className="col-md-4">
                                <a href="../images/solar%20panel%202.jpg" data-toggle="lightbox">
                                    <img src="../images/solar%20panel%202.jpg" className="img-fluid c-image" alt=""/>
                                    <h4 className="text-dark">ÉNERGIES RENOUVELABLES</h4>
                                </a>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Course_section;