import React , {Component} from 'react';

class Electricite extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomIn">
                <section className="p-4 bg-mahad text-dark">
                    <div className="container">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <h2 className="font-weight-bold text-teal">ÉLECTRICITÉ</h2>
                                <p className="lead">L'étude, la fourniture, la production, la transmission et la distribution d'énergie électrique
                                    Alimentation électrique (TGBT, Force, Automation)
                                    Réseaux électriques, UPS
                                    Installations intérieures et éclairage
                                    Éclairage extérieur et façade
                                    Éclairage d'urgence
                                    Tableaux de distribution (boucliers)
                                    Poteaux électriques et transformation HT / MT / BT
                                    générateurs
                                    Energie solaire et éolienne
                                    Système de sécurité incendie
                                    Réseau extérieur enterré
                                    Compensation énergétique
                                    Régulateurs de vitesse, démarrage progressif (étoile, triangle, stator)
                                    Afin d'étendre son champ d'action à d'autres formes telles que l'éolien et le solaire, IDROLEC a signé un accord pour former un
                                    consortium avec la société néerlandaise "INTERNATIONAL FACTORY THE WIND" pour la participation énergétique dans des projets éoliens et solaires.</p>
                            </div>
                            <div className="col-md-6">
                                <a href="../images/electricity2.jpg" data-toggle="lightbox">
                                    <img src="../images/electricity2.jpg" className="img-fluid c-image" alt=""/>
                                </a>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Electricite;