import React , {Component} from 'react';

class Relation extends Component{
    render() {
        return(
            <div className="animated zoomInDown">
                <section className="bg-sam text-primary text-center p-3">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h5 className="display-4">Nos Réalisations</h5>
                                <p className="lead">Depuis sa création IDROLEC a participé à un nombre élevé de
                                    projets nationaux visant à renforcer les capacités énergétiques
                                    nationales, à électrifier les nouvelles cités, zones et quartiers
                                    non desservies auparavant par l’électricité. Parmi ces projets
                                    menés à bien et à termes et ce conformément aux cahiers de
                                    charge établies par les autorités nationales compétentes et de la
                                    commission de passation de marché, nous pouvons citer la liste
                                    non exhaustive des projets suivants :
                                    Installation de la ligne MT d’interconnexion Djibouti/Arta
                                    Installation de la ligne MT d’interconnexion Djibouti/
                                    Loyada
                                    Electrification :
                                    Des cités HODAN I et II,
                                    Barwaqo II
                                    Tous les Quartiers de PK12
                                    Electrification des nouvelles gares (Ali-Sabieh, Dahasbio,
                                    Holl-Holl, Djibouti)
                                    Electrification du complexe immobilier et hôtelier de Tadjoura « Gadileh Palm Village »
                                    Etc.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Relation;