import React , {Component} from 'react';

class Qualite extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomInRight">
                <section className="bg-sam text-danger text-center p-3">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h5 className="display-4">POLITIQUE DE QUALITÉ</h5>
                                <p className="lead">Notre objectif est de former tout le personnel pour créer un environnement de travail qui développe des employés motivés et compétents, attachés à la mission de l'entreprise. Notre qualité conduira à
                                    une satisfaction accrue de la clientèle et à une fidélité accrue de
                                    celle-ci.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Qualite;