import React , {Component} from 'react';

class Course_contain extends Component{
    render() {
        return(
            <div className="animated zoomInLeft">
                <section className="p-3 text-center bg-omar">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-4">
                                <i className="fa fa-users text-teal fa-4x"></i>
                                <h2 className="font-weight-bold text-teal">Meilleures Employés</h2>
                            </div>
                            <div className="col-md-4">
                                <i className="fa fa-calendar text-teal fa-4x"></i>
                                <h2 className="font-weight-bold text-teal">Meilleures Services</h2>
                            </div>
                            <div className="col-md-4">
                                <i className="fa fa-book-reader text-teal fa-4x"></i>
                                <h2 className="font-weight-bold text-teal">Meilleures Guides</h2>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Course_contain;