import React , {Component} from 'react';

class Carrieres extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomInDown">
                <section className="bg-omar text-dark text-center p-3">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h5 className="display-4">CARRIERES</h5>
                                <p className="lead">Nous voulons entendre les diplômés qui partagent nos valeurs,
                                    qui ont les compétences nécessaires pour relever nos défis, qui
                                    sont motivés pour trouver des solutions aux problèmes que nos
                                    clients doivent résoudre et qui croient passionnément qu’il faut
                                    faire preuve de leadership pour gérer les projets et les personnes
                                    qui offrent ces solutions.
                                    En retour, nous vous soutiendrons dans votre développement et
                                    votre croissance personnelle, en vous offrant des possibilités de
                                    placement vous permettant de libérer votre potentiel et d'être le
                                    meilleur possible.
                                    Vous aurez été étiré et mis au défi; vous aurez pénétré sous la
                                    peau de notre entreprise et serez prêt à la faire progresser de
                                    manière stratégique, technique et innovante.
                                    Donc, si vous souhaitez faire partie d'une équipe enthousiaste et
                                    dynamique et contribuer à façonner l'avenir, envoyez-nous votre
                                    CV ;
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Carrieres;