import React , {Component} from 'react';

class Energie_Renouvelable extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomIn">
                <section className="p-4 bg-zak text-danger">
                    <div className="container">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <a href="../images/solar%20panel%202.jpg" data-toggle="lightbox">
                                    <img src="../images/solar%20panel%202.jpg" className="img-fluid c-image" alt=""/>
                                </a>
                            </div>
                            <div className="col-md-6">
                                <h2 className="font-weight-bold text-teal">ÉNERGIES RENOUVELABLES</h2>
                                <p className="lead">Les énergies renouvelables (EnR en abrégé) sont des sources d'énergie
                                    dont le renouvellement naturel est assez rapide pour qu'elles puissent être considérées
                                    comme inépuisables à l'échelle du temps humain. Elles proviennent de phénomènes naturels
                                    cycliques ou constants induits par les astres : le Soleil essentiellement pour la chaleur
                                    et la lumière qu'il génère, mais aussi l'attraction de la Lune (marées) et la chaleur
                                    générée par la Terre (géothermie). Leur caractère renouvelable dépend d'une part de la
                                    vitesse à laquelle la source est consommée, et d'autre part de la vitesse à laquelle
                                    elle se renouvelle.
                                    L'expression « énergie renouvelable » est la forme courte et usuelle des expressions
                                    « sources d'énergie renouvelables » ou « énergies d'origine renouvelable » qui sont plus
                                    correctes d'un point de vue de la physique.
                                    La part des énergies renouvelables dans la consommation finale mondiale d’énergie était
                                    estimée en 2017 à 18,1 %, dont 7,5 % de biomasse traditionnelle (bois, déchets agricoles,
                                    etc.) et 10,6 % d'énergies renouvelables « modernes » : 4,2 % de chaleur produite par
                                    les énergies renouvelables thermiques (biomasse, géothermie, solaire), 3,6 % d'hydroélectricité,
                                    2 % pour les autres renouvelables électriques (éolien, solaire, géothermie, biomasse,
                                    biogaz) et 1 % pour les biocarburants; leur part dans la production d'électricité
                                    était estimée en 2018 à 26,2 %.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Energie_Renouvelable;