import React , {Component} from 'react';

class Eau extends Component{
    render() {
        return(
            <div className="container-fluid animated zoomIn">
                <section className="p-4 bg-fouad text-primary">
                    <div className="container">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <a href="../images/eau2.jpg" data-toggle="lightbox">
                                    <img src="../images/eau2.jpg" className="img-fluid c-image" alt=""/>
                                </a>
                            </div>
                            <div className="col-md-6">
                                <h2 className="font-weight-bold text-teal">EAU</h2>
                                <p className="lead">Nos services d'eau comprennent:
                                    Exploitation et maintenance d'installations privées d'approvisionnement en eau et de traitement des eaux usées, gestion de projets et de
                                    programmes
                                    Fournir des services de construction, d’exploitation et de maintenance aux entreprises d’approvisionnement en eau et de traitement des
                                    eaux usées et aux autorités locales
                                    Offrir des compétences spécialisées en ingénierie et en projets de
                                    conception et de construction clés en main, ainsi que des produits
                                    technologiques inégalés
                                    Fourniture d'installations utilitaires complètes pour les développements de nouveaux bâtiments Fournir des solutions de recyclage pour les flux de déchets organiques
                                    Installations d'approvisionnement en eau industrielles et domestiques.
                                    Installations sanitaires et eau potable.
                                    Systèmes de pompage pour eau potable et eau usée.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>

            </div>
        );
    }
}
export default Eau;